body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-fluid{
  background-color: #3B7312;
  position: absolute;
  height:3200px;
  width:100%;
}
ul{
  margin-top:280px;
}
li{
  list-style: none;
  text-decoration: none;
  text-align: center;
}
button{
  box-shadow:7px 7px 12px black;
  border-radius: 8px;
  margin-left: -6%;
  background:linear-gradient(90deg,#85c955,#30630b);

}
h1{
  text-align: center;

}


#imgCabecera{

width:100%;
height: 280px;
background-image: url('./imagenes/field-2023250.png');
background-size: contain;
background-repeat:no-repeat;
position:absolute;


}
@media(min-width:551px){
  #imgCabecera{
    height: 320px;
  }
  ul{
    margin-top:340px;
  }
}
@media(min-width:655px){
  #imgCabecera{
    height: 420px;
  }
  ul{
    margin-top:460px;
  }
}
@media(min-width:789px){
  #imgCabecera{
    height: 520px;
  }
  ul{
    margin-top:550px;
  }
}
@media(min-width:979px){
  #imgCabecera{
    height: 620px;
  }
  ul{
    margin-top:670px;
  }
}
@media(min-width:1138px){
  #imgCabecera{
    height: 720px;
  }
  ul{
    margin-top:770px;
  }
}

@media(min-width:1442px){
  #imgCabecera{
    height: 1000px;
  }
  ul{
    margin-top:1100px;
  }
}


#header{
  background:linear-gradient(90deg,#3B7312,#204704);
}


#balon{
width:45px;
 background-image: url('./imagenes/football-147854_1280.png');
 background-size: contain;
background-repeat: no-repeat;
 height: 50px;
 margin-left: 168.5px;
margin-top: 95px;
animation:giro 4s linear infinite;


}
h3{
  text-align: center;
}
input{
  width:100%
}

@keyframes giro{
 
  0%{transform:rotateZ(0deg);transform-origin:center ;}

  100%{transform:rotateZ(359deg);}


}
